// todo: use signals to send a message when starting a new fetch 
// to cancel any previously started fetches

// small (related) bug: going through dozens of pages and then  
// clicking first- or lastLink quickly will make it 
// get stuck omn the wrong page

export default function handlePageChange(functions, props) {
	
  const [$, getData, setData] = [...functions];
	
  const pagination = +getData("pagination");
  const totalpages = +getData("totalpages");
  const setPagination = (p) => { setData("pagination", +p) };
	
  const { $row, postsRow, pageOfCurrent, pageOfCurrentName } = { ...props };
  const $postsRow = $(postsRow);
	
  const remote_location = getData("remote_location");
	const url = new URL(remote_location);

  // Update loading states
  $postsRow.classList.add("loading");
  $postsRow.querySelectorAll(".prel-header, .c-card__frame").forEach((el) => {
    el.setAttribute("aria-busy", true);
  });

  // Update pagination state (optimistic update)
  setPagination(pagination);
  if ($(pageOfCurrent)) $(pageOfCurrent).innerText = pagination;
	// console.log(pagination)
	
	// mobile (eagerly scroll back up)
	if (isTallerThanViewport()) $row.scrollIntoView();

	// set pg. number and fetch the page
  url.searchParams.set("paged", pagination);
  fetch(url.toString())
    .then((response) => {
      if (!response.ok) console.log("Response was not ok.");
      return response.text();
    })
    .then((html) => {
      if (html === "") { console.log("No HTML was received."); return; }

      // Update Content
      $postsRow.innerHTML = html;
      // Clear outer loading states
      $postsRow.classList.remove("loading");
		
			// mobile (scroll back up because fetching is done)
      if (isTallerThanViewport()) {
				// console.log($row.offsetHeight +">"+ window.innerHeight)
        scrollBackUp();
      }
    });

  function scrollBackUp() {
    $row.scrollIntoView();
		$row.querySelector("a").focus(); // focus on 1st link
    const $targetElement = $(pageOfCurrentName);
    if (!$targetElement) {
      return (console.warn(pageOfCurrentName + " not found so scrollBackUp() was stopped."));
    }
    pulsate($targetElement);
  }
  // If the row is taller than the viewport
	function isTallerThanViewport() {
		return ($row.offsetHeight > window.innerHeight);
  }
  // pulsate an element
  function pulsate(el) {
    el.classList.remove("pulsate");
    void el.offsetWidth;
    el.classList.add("pulsate");
  }
}
