import handleClicks     from "./lib/handle-clicks.js";
import handleLinkStates from "./lib/handle-link-states.js";
import pageOfComponent  from "./components/page-of.js";

const posts_rows = document.querySelectorAll(".posts-row-wrapper");

const selectors = {
  linkNext: ".next-page",
  linkPrevious: ".previous-page",
  linkLast: ".page-of--total",
  linkFirst: ".go-to-page-1",
  pageOf: ".page-of",
  pageOfCurrent: ".page-of--current",
  postsRow: ".posts-row",
  pageOfCurrentName: ".page-of--current-name",
  pageOfWrapper: "[data-slot=page-of-wrapper]",
}

// ðŸ’² Dollar prefix means the variable is a DOM el.
posts_rows.forEach( ($row) => {
  const props = {$row, ...selectors}
  // let   pagination = +getData("pagination");
  const totalpages = +getData("totalpages");
  const functions  = [$, getData, setData];
  const { linkFirst, linkLast, linkNext, linkPrevious } = {...props}
  
  // â“only run if there's more than one page
  if (!(totalpages >= 2)) {
    return;
  } 
  // $row.querySelectorAll(arrow)?.forEach(
  //   ($el) => { $el.classList.remove("hidden") }
  // );
  
  // ðŸ—º Set up a map obj. for predefined page links
  const linksMap = new Map();
  linksMap.set(linkNext, x => x+1);
  linksMap.set(linkPrevious, x => x-1);
  linksMap.set(linkLast, x => totalpages);
  linksMap.set(linkFirst, x => 1);

  // ðŸŽ¬ Init. State  
  const { pageOfWrapper, pageOf } = { ...props }
  if (!$(pageOf) && $(pageOfWrapper)) {
    $(pageOfWrapper).outerHTML = pageOfComponent(totalpages)
  }
  if ($(pageOf)) $(pageOf).classList.remove("hidden");
  if ($(linkLast)) $(linkLast).innerText = totalpages;
  handleLinkStates( functions, { linksMap, $row } );
  
  // âš—ï¸ Delegate event listeners
  $row.addEventListener("click", (event)=>{
    //event.preventDefault() // don't â€” this affects links in posts
    const $targetEl = event.target
    handleClicks($targetEl, functions, {linksMap, ...props})
  })

  // functions (all scoped to current row)
  // helper function to interact with the DOM
  function $(sel) { return $row.querySelector(sel); }
  // helper functions to access data attributes
  function getData(data) { return $("[data-"+data+"]").dataset[data]; }
  function setData(data, value) { ($("[data-"+data+"]")).dataset[data] = value; }

});