// Makes links enabled or disabled based on expected pagination,
// which, in turn, is based on the current pagination mutated using the link's mutation func.

// ! known issue: linkLast's mutate func. uses total variable (from the scope of index.js) which doesn't get its value passed through 
// because data-totalpages is not SSR'ed so not present on the initial page load.
// // UPDATE: it actually is present on the production site as <input type="hidden" data-totalpages="140">
// info: linkLast is still disabled on the last page though,
// because it fits the condition for "disable links that take to the current page"

export default function handlePaginationLinks(functions, props) {
  const [$, getData, setData] = [...functions];
  const pagination = +getData("pagination");
  const totalpages = +getData("totalpages");
	const { linksMap, $row } = { ...props }
	
	// add more convenient css selectors
	$row.classList.toggle("first-page", (pagination === 1));
	$row.classList.toggle("last-page", (pagination === totalpages));
	
	// set disabled states where they make sense (and enable everywhere else)
	for (const [link, mutatePageNumber] of linksMap) {
		if (!$(link)) continue; // if the selector doesn't exist
		const newPageNumber = +mutatePageNumber(pagination);
		// conditions to disable a type of link (as represented by a selector) 
		const pgTooBig = (newPageNumber>totalpages)
		const pgTooSmall = (newPageNumber<1)
		const pgIdentical = (newPageNumber===pagination)

		if (pgTooBig || pgTooSmall || pgIdentical) {
			$$(link).forEach(($el) => { $el.disabled = true })
		} else {
			$$(link).forEach(($el) => { $el.disabled = false })
		}
		
	}
	
	function $$(s) { return $row.querySelectorAll(s) }
}
