import handlePaginationLinks from "./handle-link-states.js";
import handlePageChange from "./handle-page-change.js";


export default function delegateEventListeners($targetEl, functions, props) {
	
	const [$, getData, setData] = [...functions];
	const p = +getData("pagination");
	const total = +getData("totalpages");
	const setPagination = (x) => { setData("pagination", Number(x)) };
	const { linksMap, $row } = { ...props }
	
	for (const [link, mutatePageNumber] of linksMap) { // loop through pre-defined map of links and their effects on pagination
		if (!isLinkValid($(link))) continue; // if invalid skip "for" iteration
		if (isPredefinedTarget(link)) {
			const newPageNumber = mutatePageNumber(p);
			doPage(newPageNumber);
		}
	}
	
	// validate p
	// store p as data-pagination att.
	// hydrate the page w/ new items
	// enable/disable UI items (pagination links)
	function doPage(p) {
		if (!isPageValid(p)) { return; }
		setPagination(p);
		handlePageChange(functions,props);
		handlePaginationLinks(functions,{linksMap,$row});
	}
	
	// Validation functions
	function isPageValid(p) {
		if (p<1) return false; 
		if (p>total) return false;
		return true;
	}
	function isLinkValid($link) {
		if (!$link) return false; // if the selector doesn't exist
		if ($link.disabled) return false; 
		return true;
	}
	function isPredefinedTarget(link) {
		// if the event bubbled up to (or was on) a link
		return $targetEl.closest(link); // aka isTargetDescendantOfLink
	}

}
